.header {
  width: 100%;
  height: 120px;
}

.Navbar {
  height: 80px;
  background-color: white;
  display: flex;
  justify-content: space-between;
  padding: 10px 75px;
}

.Navbar>img {
  width: auto;
  height: 80px;
  padding: 14px;
  margin: 15px 0px;
}

.Nav-Buttons-Wrapper {
  display: flex;
  margin-bottom: 16px;
}

.home-page {
  list-style: none;
  margin: 0 32px;
  color: #FF6060;
  cursor: pointer;
  font-size: 24px;
  margin-top: 40px;
  text-decoration: none;
  
  &.active {
    text-decoration-line: underline;
  }
}

.about {
  list-style: none;
  margin: 0 32px;
  color: #FF6060;
  cursor: pointer;
  font-size: 24px;
  margin-top: 40px;
  text-decoration: none;

  &.active {
    text-decoration-line: underline;
  }
}

@media screen and (max-width:1440px) {
  .Navbar>img {
    margin: 15px -10px;
  }
}

@media screen and (max-width: 650px) {  
  .header {
    width: 100%;
    height: 80px;    
  }
  
  .Navbar {
    padding: 10px 0px;
    margin-bottom: -20px;
  }

  .home-page, .about {
    padding: 0px 0;
    margin: 0 10px;
    font-size: 12px;  
    font-weight: 500; 
  }

  .Navbar>img {
    width: auto;
    height: 44px;
    padding: 2px;
    margin: 11px 20px;
  }
  
  .Nav-Buttons-Wrapper {
    display: flex;
    margin-top: 16px;
    text-transform: uppercase;
    padding: 13px;
  }
}
