.singleproduct {
  margin: 0 5%;
  
  @media screen and (max-width: 650px) {
    margin: 0 5%; 
  }

  &__content {
    display: flex;
    justify-content: space-between;
    margin: 30px 0;

    @media screen and (max-width: 650px) {
      display: flex;
      flex-direction: column;
      margin: 0px;
    }
  }


  &__collapse {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    list-style: none;

    @media screen and (max-width: 768px) {
      display: grid;
      grid-template-columns: repeat(1, 1fr)!important;
    }  
  }
  
  .list-item { 
    @media screen and (max-width: 650px) {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      
    }
  }
}

.group-elements-bottom {
  display: flex;
  justify-content: space-between;
}
.group-elements-left {
  display: block;
}

.name-photo-wrapper {
  display: flex;

  @media screen and (max-width: 650px) {
    float: right;
  }
}

.title {
  font-size: 36px;
  font-weight: 500;
  color: #ff6060;

  @media screen and (max-width: 650px) {
    font-size: 18px;
  }
}

.location {
  color: #ff6060;
  font-size: 18px;
  font-weight: 500;

  @media screen and (max-width: 650px) {
  font-size: 14px;
  }
}

.host-photo {
  height: 64px;
  width: 64px;
  border-radius: 50%;

  @media screen and (max-width: 650px) {
    margin-top: 0px;
  }
}

.host-name {
  margin-top: 15px;
  margin-right: 10px;
  color: #ff6060;
  width: 85px;
  text-align: right;
  font-size: 18px;
  font-weight: 500;

  @media screen and (max-width: 650px) {
    margin-top: 16px;
    font-size: 12px;
    width: 50px;
  }
}

.tags-list-wrapper {
  list-style: none;
  display: flex;
  margin-left: -10px;
  margin-top: 10px;
}

.tag {
  background-color: #ff6060;
  border-radius: 20px;
  text-align: center;
  padding: 5px 2px;
  color: white;
  margin: 8px;
  height: 25px;
  width: 115px;
}

@media screen and (min-width: 768px) {
  .left-icon svg {
    width: 48.68px;
    height: 79.2px;
    position: relative;
    left: -60%;
    top: 20px;
  }

  .right-icon svg {
    width: 48.68px;
    height: 79.2px;
    position: relative;
    right: -60%;
    top: 20px;
  }
}

@media screen and (max-width: 650px) {
  .left-icon svg {
    width: 20px;
    height: 20px;
  }

  .right-icon svg {
    width: 20px;
    height: 20px;
  }
}
