.page-not-found {
  width: 100%;
  height: 70vh;
  text-align: center;

  h1 {
    font-size: 288px;
    margin: 70px 0;
    color: #ff6060;
  }

  h2 {
    color: #ff6060;
    font-size: 36px;
    font-weight: 500;
  }
}

.return-to-home {
  color: #ff6060;
  margin-top: 182px;
  a {
    color: #ff6060;
  }
}

@media screen and (max-width: 992px) {
  .page-not-found {
    height: auto;

    h1 {
      font-size: 180px;
      margin-top: 144px;
      color: #ff6060;
    }

    h2 {
      display: flex;
      align-items: center;
      text-align: center;
      position: absolute;
      left: 5%;      
    }
  }
}

@media screen and (max-width: 650px) {
  .page-not-found {
    height: auto;

    h1 {
      font-size: 96px;
      margin-top: 144px;
      color: #ff6060;
    }

    h2 {
      display: flex;
      align-items: center;
      text-align: center;
      position: absolute;
      left: 15.2%;
      right: 15.47%;
      top: 55.81%;
      bottom: 50%;
      font-size: 18px;      
    }
  }
}
