footer {
  background-color: rgb(0, 0, 0);
  width: 100%;
  padding-top: 24px;
  text-align: center;
  font-weight: 500;
  margin-top: 40px;
  margin-left: 0;
  height: 209px;

  img {
    margin-top: 40px;
  }

  p {
    color: white;
    margin-top: 40px;
  }
}

@media screen and (max-width: 650px) {
  footer {          
    img {
      margin-top: 25px;
    }
    
    p {
      font-size: 12px!important;
      margin-top: 15px!important;
    }
  }
}
